  头部
-->
<template>
  <div class="header">
    <clock></clock>
    <!--脉冲动效-->
    <pulse class="pulse1" :width="100" :skewDir="0"></pulse>
    <pulse class="pulse2" :width="100" :skewDir="1"></pulse>

    <yearSlider v-if="isShow&&flag" @change="handleYearSlider"></yearSlider>
    <dateSlider v-if="isShow&&!flag"></dateSlider>
  </div>
</template>

<script>
  import clock from '@/components/clock/index'
  import pulse from '@/components/pulse/index';
  import yearSlider from '@/components/yearSlider/index'
  import dateSlider from '@/components/dateSlider/index'

  export default {
    name: "index",
    components: {
      clock,
      pulse,
      yearSlider,
      dateSlider,
    },
    data() {
      return {
        isShow: true
      }
    },
    methods: {
      // 年份切换
      handleYearSlider(year) {
        console.log(year);
        // 提交vuex
        this.$store.commit({
          type: 'setYear',
          year: year
        });
      }
    },
    computed: {
      flag() {
       //矛盾分析预警预判-现状分析、矛盾分析预警预判-预警预测 两个页面使用年份筛选 其余页面使用日期筛选
        return this.$route.path==='/mdfxyjyp/xzfx'||this.$route.path==='/mdfxyjyp/yjyc'
      }
    },
    watch: {
      $route(to, from) {
        // 信访总量预警-总量预警都不显示
        this.isShow = !(to.path === '/xfzlyj/zlyj'||to.path === '/mdfxyjyp/yjyc');
      }
    }
  }
</script>

